.hero__background {
  background-size: contain;
  position: absolute;
  z-index: 0;

  width: 100%;
  height: 100%;
  top: 0;
  background-repeat: no-repeat;

  @media screen and (min-width: 768px) {
    background-position: px top, calc(184px / 2) 0;
    background-image: url('../img/dark-hero-tablet.png');
    @include retina {
      background-image: url('../img/dark-hero-tablet-2x.png');
    }
    z-index: 0;
    background-size: contain;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    bottom: 0;
    background-repeat: no-repeat;
  }

  @media screen and (min-width: 1200px) {
    background-position: 0px top, calc(184px / 2) 0;
    background-image: url('../img/dark-hero-desktop.png');
    @include retina {
      background-image: url('../img/dark-hero-desktop-2x.png');
    }
    z-index: 0;
    background-size: contain;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    background-repeat: no-repeat;
  }
}

.hero__box {
  position: relative;
  z-index: 2;
}

.hero {
  margin-top: -1px;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;
  min-height: 380px;
  background-color: #0e0e0e;
}

.hero__with-film {
  padding: 118px 0 118px;
  background-image: linear-gradient(
      87.8deg,
      #0e0e0e 15.61%,
      rgba(14, 14, 14, 0) 60.39%
    ),
    url(../img/hero-mobile.jpg);
  @include retina {
    background-image: linear-gradient(
        87.8deg,
        #0e0e0e 15.61%,
        rgba(14, 14, 14, 0) 60.39%
      ),
      url(../img/hero-mobile-2x.jpg);
  }
  background-color: #100d0d;
  background-position: center;

  background-size: cover;
  background-repeat: no-repeat;
  max-width: 1600px;
  margin: 0 auto;
  margin-bottom: 60px;

  @media screen and (min-width: 768px) {
    margin-bottom: 0;
    background-position: 5px top, calc(184px / 2) 0;
    background-image: url(../img/hero-tablet.jpg);

    @include retina {
      background-image: url(../img/dark-hero-tablet-2x.png),
        url(../img/hero-tablet-2x.jpg);
    }
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 0;
    padding: 0;
    height: 720px;
    background-size: cover;
    background-position: 5px top, calc(484px / 2) 0;
    background-image: url(../img/dark-hero-desktop.png),
      url(../img/hero-desktop.jpg);

    @include retina {
      background-image: url(../img/dark-hero-desktop-2x.png),
        url(../img/hero-desktop-2x.jpg);
    }
  }
}

