.hero__title {
  margin-left: 20px;
  margin-bottom: 8px;
  max-width: 200px;

  font-weight: 900;
  font-size: 32px;

  letter-spacing: 0.06em;

  text-align: left;
  padding: 0;

  color: $text-dark-background;

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    margin-left: 32px;
    max-width: 299px;
    font-size: 35px;
    letter-spacing: 0;
    line-height: 51.56px;
  }

  @media screen and (min-width: 1200px) {
    padding-top: 169px;
    margin-left: 32px;
    max-width: 464px;
    font-size: 58px;
    letter-spacing: 0;
    line-height: 75px;

    color: rgba(255, 255, 255, 1);
  }
  @media screen and (min-width: 1200px) {
    padding-top: 169px;
    margin-left: 32px;
    max-width: 464px;
    font-size: 58px;
    letter-spacing: 0;
    line-height: 75px;
  }
}

