.hero__overview {
  margin-bottom: 36px;
  margin-left: 20px;
  color: $text-secondary-color;
  max-width: 367px;
  font-size: 14px;
  max-width: 180px;

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    margin-left: 32px;
    max-width: 307px;
    font-size: 14px;
    letter-spacing: 0;
  }

  @media screen and (min-width: 1200px) {
    margin-left: 32px;
    max-width: 464px;
    font-size: 16px;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0em;
    text-align: left;
  }
}

.hero__btn {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-decoration: none;

  margin-left: 20px;
  padding: 12px 24px;

  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-radius: 74px;
  color: $text-light-background;
  // text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);;

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    margin-left: 32px;
  }

  @media screen and (min-width: 1200px) {
    margin-left: 32px;
  }
}

