h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.link, a {
  text-decoration: none;
  cursor: pointer;
}

.list {
  list-style: none;
}

button {
  cursor: pointer;
  outline: none;
}