#scroll-to-top {
  opacity: 0;
  z-index: 50;
  background: none;
  border: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: transform 1s ease-in-out, opacity 1s ease-in-out;
  display: flex;
  position: fixed;
  bottom: 50px;
  right: 50px;
  transform: translateY(30%);
}

.arrow {
  width: 20px;
  height: 20px;
  border-bottom: 4px solid #ffc226;
  border-right: 4px solid #ffc226;
  margin: -10px;
  animation: 2s infinite animate;
}

@media screen and (min-width: 768px) {
  .arrow {
    width: 25px;
    height: 25px;
    margin: -5px;
  }
}

@media screen and (min-width: 1200px) {
  .arrow {
    width: 30px;
    height: 30px;
  }
}

.arrow:nth-child(2) {
  border-bottom: 4px solid #fb801f;
  border-right: 4px solid #fb801f;
  animation-delay: -.2s;
}

.arrow:nth-child(3) {
  border-bottom: 4px solid #f84119;
  border-right: 4px solid #f84119;
  animation-delay: -.4s;
}

@keyframes animate {
  0% {
    opacity: 0;
    transform: rotate(-135deg)translate(-15px, -15px);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: rotate(-135deg)translate(15px, 15px);
  }
}

