//       

@mixin mobile {
  @media screen and (min-width: 480px) {
    @content;
  }
}

@mixin tablet {
  @media screen and (min-width: 768px) {
    @content;
  }
}

@mixin desktop {
  @media screen and (min-width: 1200px) {
    @content;
  }
}
//

//      

@mixin retina {
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    @content;
  }
}
//

//      

@mixin centerFlex() {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin centerAlingn() {
  display: flex;
  align-items: center;

}
//

//     

@mixin font($fw, $fs, $lh, $ls) {
  font-weight: $fw;

  font-size: $fs + px;

  line-height: $lh;

  letter-spacing: $ls + em;
}

//         

@mixin transition($type) {
  transition: $type 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

@mixin transitionX2($type1, $type2) {
  transition: $type1 300ms cubic-bezier(0.4, 0, 0.2, 1),
    $type2 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

@mixin transitionX3($type1, $type2, $type3) {
  transition: $type1 300ms cubic-bezier(0.4, 0, 0.2, 1),
    $type2 300ms cubic-bezier(0.4, 0, 0.2, 1),
    $type3 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

@mixin transitionX4($type1, $type2, $type3, $type4) {
  transition: $type1 300ms cubic-bezier(0.4, 0, 0.2, 1),
    $type2 300ms cubic-bezier(0.4, 0, 0.2, 1),
    $type3 300ms cubic-bezier(0.4, 0, 0.2, 1),
    $type4 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes Glow {
  from {
    text-shadow: 0 0 10px $accent, 0 0 20px $accent,
      0 0 30px $accent, 0 0 40px $accent, 0 0 70px $accent,
      0 0 80px$accent, 0 0 100px$accent, 0 0 150px$accent;
  }
  to {
    text-shadow: 0 0 5px $accent, 0 0 10px $accent,
      0 0 15px $accent, 0 0 20px$accent, 0 0 35px $accent,
      0 0 40px $accent, 0 0 50px $accent, 0 0 75px $accent;
  }
}